<template>
  <div class="task-list">
    <taskItem :list="recordList"/>
    <div class="loading-wrap" v-show="isLoading">
  		<i></i>
  		<span>加载中...</span>
  	</div>
	  <div class="loaded" v-show="isOver">没有更多数据</div>
  </div>
</template>
<script>
import taskItem from './taskItem.vue'
export default {
  name: 'taskList',
  data () {
    return {
      page:0,
      size:10,
      recordList:[],
      isLoading: true,
      isOver: false
    }
  },
  components:{
    taskItem
  },
  methods:{
    getTasklist(that,page){
      that.$ajax({
        method: 'GET',
        url: `${that.api}/api/record?page=${page}&size=${that.size}&sort=desc`
       }).then(function (response) {
          if(response.data.content.length < that.size) {
    				that.isOver = true;
    				that.isLoading = false;
    			}
  			  that.recordList = that.recordList.concat(response.data.content)
          console.log(that.recordList);
       }).catch(function (error) {
          console.log(error);
       });
    }
  },
  mounted() {
    var that=this;
    window.onscroll = function() {
      var screenHeight = window.screen.height;
      var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
      var scrollHeight = document.body.scrollHeight || document.documentElement.scrollHeight;
      if (screenHeight + scrollTop >= scrollHeight) {
        if (that.isOver) {
          return;
        }
        that.page++;
        that.getTasklist(that,that.page);
      }
    };
  },
  created(){
    this.getTasklist(this,this.page)

  }
}
</script>



