<template>
  <div class="task-container">
    <div class="task-item" v-for="(item,index) in list">
      <span class="name">{{nameFormate(item.name)}}</span>
      <div class="item-content">
        <div class="item-top">
          <span>{{nameFormate(item.name)}}<span class="item-work">{{item.action}}</span></span>
          <span>{{item.createTime | parseTime('{y}.{m}.{d} {h}:{i}')}}</span>
        </div>
        <div class="work-dec">{{item.taskTitle}}</div>
        <div class="cancel-reason" v-if="item.cancelReason">{{item.cancelReason}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'taskItem',
  data () {
    return {

    }
  },
  props:{
     list:{
        type:Array
     }
  },
  methods:{
    nameFormate(name){
      if(name.length>=2){
        return name.substr(-2,1)+name.substr(-1,1)
      }else{
        return name
      }
    }
  },
  created(){

  }
}
</script>



